  font-size: 100%;
  font-family: 'Ubuntu', sans-serif;
  color: #14151f;
} */
body.mobile-open {
  overflow-y: hidden;
}
a {
  text-decoration: none;
  color: inherit;
}
h1 {
  font-size: 2.5rem;
  margin: 20px 0;
}
h2 {
  font-size: 2rem;
  margin: 16px 0;
}
p {
  font-size: 1.1rem;
  line-height: 1.6rem;
}
.container {
  max-width: 1024px;
  margin: 0 auto;
}
.mobile-nav {
  background: #333;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  z-index: 999;
  transition: -webkit-transform .3s ease-in .7s;
  transition: transform .3s ease-in .7s;
  transition: transform .3s ease-in .7s, -webkit-transform .3s ease-in .7s;
  display: none;
}
.mobile-open .mobile-nav {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  transition: -webkit-transform .3s ease-out;
  transition: transform .3s ease-out;
  transition: transform .3s ease-out, -webkit-transform .3s ease-out;
}
.mobile-nav .links {
  width: 100%;
  height: 100%;
  text-align: center;
}
.mobile-nav .links a {
  position: absolute;
  left: 50%;
  color: #FFF;
  font-size: 1.5rem;
}
.mobile-nav .links a:nth-child(1) {
  -webkit-transform: translate(-50%, 30vh);
          transform: translate(-50%, 30vh);
}
.mobile-nav .links a:nth-child(2) {
  -webkit-transform: translate(-50%, 40vh);
          transform: translate(-50%, 40vh);
}
.mobile-nav .links a:nth-child(3) {
  -webkit-transform: translate(-50%, 50vh);
          transform: translate(-50%, 50vh);
}
.mobile-nav .links a:nth-child(4) {
  -webkit-transform: translate(-50%, 60vh);
          transform: translate(-50%, 60vh);
}
nav {
  position: fixed;
  width: 100%;
  background: #F1F5F8;
  background: var(--background-main);
  padding: 1rem;
  border-bottom: solid 1px #CCC;
  z-index: 9999;
}
nav .container {
  display: flex;
  position: relative;
}
nav .logo {
  width: 100px;
  font-size: 2em;
  text-transform: lowercase;
}
nav .logo span:nth-child(n+0) {
    color: #2779BD;
    color: var(--blue-1);
}
nav .logo span:nth-child(2n+0) {
    color: #38A89D;
    color: var(--teal-1);
}
nav .logo span:nth-child(3n+0) {
  color: #5661B3;
  color: var(--indigo-1);
}
nav .logo span:nth-child(4n+0) {
  color: #EB5286;
  color: var(--pink-1);
}
/* nav .logo span:nth-child(5) {
  color: var(--blue-1);
}
nav .logo span:nth-child(6) {
  color: var(--teal-1); 
} */
nav .burger {
  position: absolute;
  right: 0;
  top: calc(50% - 15px);
  height: 30px;
  width: 35px;
  cursor: pointer;
  display: none;
}
nav .burger .menu, nav .burger .close {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
nav .burger .menu {
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
nav .burger .close {
  z-index: 1;
}
nav .burger .line {
  height: 3px;
  -webkit-transform-origin: right;
          transform-origin: right;
  background: #222;
}
nav .burger .close .line {
  -webkit-transform-origin: center;
          transform-origin: center;
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  opacity: 0;
  transition: all .4s ease-in;
}
.mobile-open nav .burger .close .line:first-child {
  -webkit-transform: rotate(225deg);
          transform: rotate(225deg);
  opacity: 1;
  transition: all .6s ease-in 1s;
}
.mobile-open nav .burger .close .line:last-child {
  -webkit-transform: rotate(-225deg);
          transform: rotate(-225deg);
  opacity: 1;
  transition: all .6s ease-in 1s;
}
nav .burger .menu .line:nth-child(1) {
  background: #38A89D;
  background: var(--teal-1);
  -webkit-animation: 1s ease-out backwards returnLine1;
          animation: 1s ease-out backwards returnLine1;
}
.mobile-open .burger .menu .line:nth-child(1) {
  -webkit-animation: .8s ease-in forwards moveLine1;
          animation: .8s ease-in forwards moveLine1;
}
nav .burger .menu .line:nth-child(2) {
  background: #2779BD;
  background: var(--blue-1);
  -webkit-animation: 1s ease-out .2s backwards returnLine2;
          animation: 1s ease-out .2s backwards returnLine2;
}
.mobile-open .burger .menu .line:nth-child(2) {
  -webkit-animation: .8s ease-in .15s forwards moveLine2;
          animation: .8s ease-in .15s forwards moveLine2;
}
nav .burger .menu .line:nth-child(3) {
  background: #5661B3;
  background: var(--indigo-1);
  -webkit-animation: 1s ease-out .4s backwards returnLine3;
          animation: 1s ease-out .4s backwards returnLine3;
}
.mobile-open .burger .menu .line:nth-child(3) {
  -webkit-animation: .8s ease-in .3s forwards moveLine3;
          animation: .8s ease-in .3s forwards moveLine3;
}
nav .burger .menu .line:nth-child(4) {
  background: #EB5286;
  background: var(--pink-1);
  -webkit-animation: 1s ease-out .6s backwards returnLine4;
          animation: 1s ease-out .6s backwards returnLine4;
}
.mobile-open .burger .menu .line:nth-child(4) {
  -webkit-animation: .8s ease-in .45s forwards moveLine4;
          animation: .8s ease-in .45s forwards moveLine4;
}
.no-animation nav .burger .line {
  -webkit-animation: none !important;
          animation: none !important;
}
nav .links {
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
}
.link {
  margin-left: 25px;
  padding: 0 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}
.link a {
  display: block;
  font-size: 1.2rem;
  transition: color .25s ease-out;
}
.link .underline {
  width: 100%;
  display: flex;
  position: absolute;
  bottom: 5px;
}
.link .underline .part {
  height: 4px;
  flex: 0 0 25%;
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
  opacity: 0;
  transition: all .2s ease-out;
}
.underline .part:nth-child(1) {
  transition-delay: 0s;
}
.underline .part:nth-child(2) {
  transition-delay: .075s;
}
.underline .part:nth-child(3) {
  transition-delay: .15s;
}
.underline .part:nth-child(4) {
  transition-delay: .225s;
}

.link.teal .part:nth-child(1) {
  background: #38A89D;
  background: var(--teal-1);
}
.link.teal .part:nth-child(2) {
  background: linear-gradient(90deg, #38A89D, #4DC0B5);
  background: linear-gradient(90deg, var(--teal-1), var(--teal-2));
}
.link.teal .part:nth-child(3) {
  background: linear-gradient(90deg, #4DC0B5, #64D5CA);
  background: linear-gradient(90deg, var(--teal-2), var(--teal-3));
}
.link.teal .part:nth-child(4) {
  background: linear-gradient(90deg, #64D5CA, #A0F0ED);
  background: linear-gradient(90deg, var(--teal-3), var(--teal-4));
}
.link.blue .part:nth-child(1) {
  background: #2779BD;
  background: var(--blue-1);
}
.link.blue .part:nth-child(2) {
  background: linear-gradient(90deg, #2779BD, #3490DC);
  background: linear-gradient(90deg, var(--blue-1), var(--blue-2));
}
.link.blue .part:nth-child(3) {
  background: linear-gradient(90deg, #3490DC, #6CB2EB);
  background: linear-gradient(90deg, var(--blue-2), var(--blue-3));
}
.link.blue .part:nth-child(4) {
  background: linear-gradient(90deg, #6CB2EB, #BCDEFA);
  background: linear-gradient(90deg, var(--blue-3), var(--blue-4));
}
.link.indigo .part:nth-child(1) {
  background: #5661B3;
  background: var(--indigo-1);
}
.link.indigo .part:nth-child(2) {
  background: linear-gradient(90deg, #5661B3, #6574CD);
  background: linear-gradient(90deg, var(--indigo-1), var(--indigo-2));
}
.link.indigo .part:nth-child(3) {
  background: linear-gradient(90deg, #6574CD, #7886D7);
  background: linear-gradient(90deg, var(--indigo-2), var(--indigo-3));
}
.link.indigo .part:nth-child(4) {
  background: linear-gradient(90deg, #7886D7, #B2B7FF);
  background: linear-gradient(90deg, var(--indigo-3), var(--indigo-4));
}
.link.pink .part:nth-child(1) {
  background: #EB5286;
  background: var(--pink-1);
}
.link.pink .part:nth-child(2) {
  background: linear-gradient(90deg, #EB5286, #F66D9B);
  background: linear-gradient(90deg, var(--pink-1), var(--pink-2));
}
.link.pink .part:nth-child(3) {
  background: linear-gradient(90deg, #F66D9B, #FA7EA8);
  background: linear-gradient(90deg, var(--pink-2), var(--pink-3));
}
.link.pink .part:nth-child(4) {
  background: linear-gradient(90deg, #FA7EA8, #FFBBCA);
  background: linear-gradient(90deg, var(--pink-3), var(--pink-4));
}

.link.teal a:hover {
  color: #38A89D;
  color: var(--teal-1);
}
.link.blue a:hover {
  color: #2779BD;
  color: var(--blue-1);
}
.link.indigo a:hover {
  color: #5661B3;
  color: var(--indigo-1);
}
.link.pink a:hover {
  color: #EB5286;
  color: var(--pink-1);
}
.link a:hover + .underline .part {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

@media (max-width: 768px) {
  .mobile-nav {
    display: initial;
  }
  nav {
    padding: 1rem 2rem;
  }
  nav .burger {
    display: flex;
  }
  nav .links {
    display: none;
  }
}

@-webkit-keyframes moveLine1 {
  5% {
